import { CodeSlashOutline, HeartOutline, SchoolOutline } from "react-ionicons";
import styled from "styled-components";
import { MainSection, SectionTitle, SectionHeading } from "./Elements";
import { ResponsiveContainer } from "./ResponsiveContainer";

import {
  CssThree,
  Express,
  Figma,
  Html5,
  Java,
  Javascript,
  Mongodb,
  Nodedotjs,
  ReactJs,
  Styledcomponents,
} from "@icons-pack/react-simple-icons";

export default function About() {
  return (
    <ResponsiveContainer>
      <AboutStyles id="about">
        <SectionTitle href="about">ABOUT</SectionTitle>
        <MainSection>
          <h1>
            <span>Hey, I'm</span> Audrey{" "}
            <img
              style={{
                width: "52px",
                marginLeft: "-2px",
                marginBottom: "-2px",
              }}
              src="/img/smiley.png"
              alt="smiley face"
            />
          </h1>
          <img
            className="headshot"
            src="/img/audrey.png"
            alt="audrey in a garden"
          />
          <ul className="details__list">
            <li>
              <CodeSlashOutline color="#485A3D" width="20px" height="20px" />
              <p>
                I'm a Seattle-based software developer who is passionate about
                frontend development and design.
              </p>
            </li>
            <li>
              <SchoolOutline color="#485A3D" width="20px" height="20px" />
              <p>
                I'm majoring in{" "}
                <a
                  href="https://ischool.uw.edu/"
                  target="_blank"
                  rel="noreferrer"
                >
                  Informatics
                </a>
                &nbsp;with a focus in software development, HCI, and
                cybersecurity at the{" "}
                <a
                  href="https://www.washington.edu/"
                  target="_blank"
                  rel="noreferrer"
                >
                  University of Washington
                </a>
                .
              </p>
            </li>
            <li>
              <HeartOutline color="#485A3D" width="20px" height="20px" />
              <p>
                I'm always craving for coconut water &amp;{" "}
                <a
                  href="https://vanleeuwenicecream.com/product/vanilla-bean-ice-cream/"
                  target="_blank"
                  rel="noreferrer"
                >
                  vanilla-flavored
                </a>{" "}
                desserts. I also love to cook and bake in my free time!
              </p>
            </li>
          </ul>
          <SectionHeading>RECENT LANGUAGES &amp; TECHNOLOGIES:</SectionHeading>
          <ul className="skills__list">
            <li>
              <Java title="Java" width="36" height="36" />
              <h3>Java</h3>
            </li>
            <li>
              <Html5 title="HTML" width="36" height="36" />
              <h3>HTML</h3>
            </li>
            <li>
              <CssThree title="CSS" width="36" height="36" />
              <h3>CSS</h3>
            </li>
            <li>
              <Javascript title="JavaScript" width="36" height="36" />
              <h3>JavaScript</h3>
            </li>
            <li>
              <ReactJs title="React.js" width="36" height="36" />
              <h3>React.js</h3>
            </li>
            <li>
              <Styledcomponents
                title="styled-components"
                width="36"
                height="36"
              />
              <h3>styled-components</h3>
            </li>
            <li>
              <Nodedotjs title="Node.js" width="36" height="36" />
              <h3>Node.js</h3>
            </li>
            <li>
              <Express title="Express" width="36" height="36" />
              <h3>Express</h3>
            </li>
            <li>
              <Mongodb title="MongoDB" width="36" height="36" />
              <h3>MongoDB</h3>
            </li>
            <li>
              <Figma title="Figma" width="36" height="36" />
              <h3>Figma</h3>
            </li>
          </ul>
        </MainSection>
      </AboutStyles>
    </ResponsiveContainer>
  );
}

const AboutStyles = styled.section`
  margin-top: 132px;
  margin-bottom: 160px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;

  h1 {
    font-size: 68px;
    letter-spacing: 2px;
    margin: -6px 0 0;
    line-height: 68px;
    cursor: default;

    span {
      color: #a3b994;
    }

    @media (max-width: 820px) {
      font-size: 56px;
    }

    @media (min-width: 768px) {
      &:hover + .headshot {
        display: block;
      }
    }
  }

  .headshot {
    display: none;

    @media (min-width: 768px) {
      position: absolute;
      top: -98px;
      right: 26%;
      width: 180px;
      opacity: 0.9;
    }
  }

  .details__list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    list-style: none;
    margin: 28px 0 42px;
    padding: 0;

    li + li {
      margin-left: 20px;
    }

    li {
      display: flex;
      align-items: flex-start;
      max-width: 315px;
      letter-spacing: 1px;
      margin: 12px 0 20px;
      padding: 0;
      line-height: 1.5;

      span {
        margin-right: 16px;
      }

      p {
        margin: 0;
      }

      a {
        position: relative;
        background-image: linear-gradient(to left, #889c80, #889c80);
        background-repeat: repeat-x;
        background-position: 0 92%;
        background-size: 1px 1px;
      }
    }

    @media (max-width: 1190px) {
      li {
        margin-left: 0 !important;
      }
    }
  }

  .skills__list {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 0;
    list-style: none;
    overflow: auto;
    margin: 16px -24px;

    li {
      max-width: 78px;
      margin: 12px 24px;

      svg {
        display: block;
        margin: 0 auto 12px;
      }

      h3 {
        margin: 0;
        font-family: "Whyte Medium", sans-serif;
        font-weight: 400;
        font-size: 10px;
        letter-spacing: 1px;
        text-align: center;
        text-transform: uppercase;
      }
    }
  }
`;
